import axios from "axios";
import React, { Component } from "react";
import { BiExport, BiSort } from "react-icons/bi";
import { FiEdit } from "react-icons/fi";
import { ImFilesEmpty } from "react-icons/im";
import {
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdLocationCity,
} from "react-icons/md";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../../actions";
import { getAllRoom } from "../../../actions/location";
import { PopupAlertData } from "../../../components/Alert/Popup";
import {
  generatePDF,
  reportSettingsInterface,
} from "../../../components/DownloadPDF/rest";
import TableLoading from "../../../components/Loading/TableLoading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { SideNavigation } from "../../../components/Navigation/SideNavigation";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import {
  invigilatorReportContentInterface,
  invigilatorReportContentKeyInterface,
  invigilatorToPrintInterface,
  UserByTypeInterface,
  UserByTypeInterfaceAPI,
  UserByTypeKeys,
} from "../../../interfaces/user_by_type";
import { StoreState } from "../../../reducers";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import {
  LocationRoomTableInterface,
  RoomLocationInterface,
} from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";
import ExportToExcel from "../../../utils/ExportToExcel";
import { sortThisArray } from "../../../utils/functions";
import { LocationsInArray } from "../../UserManagement/UserList";
import ChooseLocation from "./ChooseLocation";

const defaultReportContent: invigilatorReportContentInterface[] = [
  {
    key: invigilatorReportContentKeyInterface.province_name,
    title: "Province name",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.district_name,
    title: "District name",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.center_name,
    title: "Center name",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.first_name,
    title: "First name",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.last_name,
    title: "Last name",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.national_id,
    title: "National ID",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.gender,
    title: "Gender",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.email,
    title: "Email",
    status: true,
  },
  {
    key: invigilatorReportContentKeyInterface.phone_number,
    title: "Phone",
    status: true,
  },
];

interface InvigilatorReportProps {
  //   REDUX ############################
  auth: Auth;
  locations: RoomLocationInterface[];
  getAllRoom: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface InvigilatorReportState {
  loading: boolean;
  selectAll: boolean;
  reportMainData: UserByTypeInterfaceAPI[] | null;
  reportContentExport: invigilatorReportContentInterface[];
  reportTitle: string;
  centerToPrint: invigilatorToPrintInterface[] | null;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
  searchInput: string;
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  selectedProvince: LocationRoomTableInterface | "all";
  chooseLocation: "province" | "district" | "center" | null;
  selectedDistrict: LocationRoomTableInterface | "all";
  selectedCenter: LocationRoomTableInterface | "all";
  downloading: boolean;
}

class index extends Component<InvigilatorReportProps, InvigilatorReportState> {
  constructor(props: InvigilatorReportProps) {
    super(props);

    this.state = {
      loading: true,
      selectAll: true,
      reportMainData: null,
      reportContentExport: defaultReportContent,
      reportTitle: "",
      centerToPrint: null,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      searchInput: "",
      generateReportModal: null,
      formError: null,
      selectedProvince: "all",
      chooseLocation: null,
      selectedDistrict: "all",
      selectedCenter: "all",
      downloading: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  setSelectAll = (status: boolean) => {
    this.setState({ selectAll: status });
  };

  setReportTitle = (thisTitle: string) => {
    this.setState({ reportTitle: thisTitle });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  loadInvigilator = async () => {
    try {
      const res = await axios.get<UserByTypeInterfaceAPI[]>(
        `${API.AUTH}/userbytype/${UserByTypeKeys.INVIGILATOR}`
      );
      console.log("====================================");
      console.log({ reportMainData: res.data });
      console.log("====================================");
      this.setState({ reportMainData: res.data });
    } catch (error) {
      console.error({ reportMainData_error: error });
      // callback(false, errorToText(error));
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  setSortOrder = (thisOrder: "ASC" | "DESC") => {
    this.setState({ sortOrder: thisOrder });
  };

  setSortProperty = (thisProperty: string) => {
    this.setState({ sortProperty: thisProperty });
  };

  setIsSorted = (status: boolean) => {
    this.setState({ isSorted: status });
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
    this.setIsSorted(true);
  };

  setGenerateReportModal = (
    data: {
      reportType: "EXCEL" | "PDF";
      status: boolean;
    } | null
  ) => {
    this.setState({ generateReportModal: data });
  };

  componentDidMount = () => {
    if (this.state.reportMainData === null) {
      this.loadInvigilator();
    }
    if (this.state.loading) {
      this.props.getAllRoom(
        (
          status: boolean,
          errorMsg: string,
          resData?: RoomLocationInterface[]
        ) => {
          this.setLoading(status);
          if (!status) {
            if (!isEmptyOrSpaces(errorMsg)) {
              this.props.PushPopupAlert({
                title: errorMsg,
                theme: PopupAlertTheme.warning,
              });
            }
          }
        }
      );
    }
  };

  showProvince = (thisProvince: string): string => {
    let result: string = thisProvince + " province";

    if (thisProvince.toLocaleLowerCase().includes("kigali")) {
      result = thisProvince + " city";
    }
    return result;
  };

  updateContentToPrint = () => {
    // UPDATE REPORT TITLE ========================
    let title = "Room with invigilator report";
    if (this.state.selectedCenter !== "all") {
      title =
        "Room with invigilator in " +
        this.showProvince(this.state.selectedCenter.province_name) +
        " - " +
        this.state.selectedCenter.district_name +
        " district - " +
        this.state.selectedCenter.center_name +
        " report";
    } else if (this.state.selectedDistrict !== "all") {
      title =
        "Room with invigilator in " +
        this.showProvince(this.state.selectedDistrict.province_name) +
        " - " +
        this.state.selectedDistrict.district_name +
        " district report";
    } else if (this.state.selectedProvince !== "all") {
      title =
        "Room with invigilator in " +
        this.showProvince(this.state.selectedProvince.province_name) +
        " report";
    }
    this.setReportTitle(title);

    // UPDATE CONTENT =============================
    if (this.state.reportMainData) {
      let newReportMainData = this.getNeededData(
        LocationsInArray(this.props.locations),
        this.state.reportMainData
      );
      newReportMainData = this.FC_FilterReportData(
        newReportMainData,
        this.state.selectedProvince,
        this.state.selectedDistrict,
        this.state.selectedCenter
      );
      let arrayNew: invigilatorToPrintInterface[] = [];
      let n: number = 1;
      for (const key in newReportMainData) {
        if (Object.prototype.hasOwnProperty.call(newReportMainData, key)) {
          let dataArray: invigilatorToPrintInterface | null = null;
          dataArray = {
            no: n,
            province_name: newReportMainData[key].province_name,
            district_name: newReportMainData[key].district_name,
            center_name: newReportMainData[key].center_name,
            room_name: newReportMainData[key].room_name,
            first_name: newReportMainData[key].first_name
              ? newReportMainData[key].first_name!
              : "",
            last_name: newReportMainData[key].last_name
              ? newReportMainData[key].last_name!
              : "",
            gender: newReportMainData[key].gender
              ? newReportMainData[key].gender!
              : "",
            national_id: newReportMainData[key].national_id
              ? newReportMainData[key].national_id!
              : "",
            phone_number: newReportMainData[key].phone_number
              ? newReportMainData[key].phone_number!
              : "",
            email: newReportMainData[key].email
              ? newReportMainData[key].email!
              : "",
          };

          if (this.state.reportContentExport) {
            for (const keyDelete in this.state.reportContentExport) {
              if (
                Object.prototype.hasOwnProperty.call(
                  this.state.reportContentExport,
                  keyDelete
                )
              ) {
                if (
                  !this.state.reportContentExport[keyDelete].status &&
                  dataArray
                ) {
                  delete dataArray[
                    this.state.reportContentExport[keyDelete].key
                  ];
                }
              }
            }
          }
          dataArray && arrayNew.push(dataArray);
          n++;
        }
      }
      this.setState({ centerToPrint: arrayNew });
      arrayNew = [];
      n = 1;
    }
  };

  updateReportContent = (key: invigilatorReportContentKeyInterface) => {
    if (this.state.reportContentExport) {
      let index: number = this.state.reportContentExport.findIndex(
        (item) => item.key === key
      );
      let newArray: invigilatorReportContentInterface[] =
        this.state.reportContentExport;
      newArray[index].status = !newArray[index].status;
      this.setState({ reportContentExport: newArray });
      let isSelectAllNeeded = newArray.find((item) => item.status === false);
      if (isSelectAllNeeded) {
        this.setSelectAll(false);
      } else {
        this.setSelectAll(true);
      }

      // UPDATE APPLICATION LIST FOR UPDATE ==============================
      this.updateContentToPrint();
    }
  };

  isChangeAllowed = (key: invigilatorReportContentKeyInterface): boolean => {
    if (
      key === invigilatorReportContentKeyInterface.center_name ||
      key === invigilatorReportContentKeyInterface.district_name ||
      key === invigilatorReportContentKeyInterface.room_name ||
      key === invigilatorReportContentKeyInterface.first_name ||
      key === invigilatorReportContentKeyInterface.last_name ||
      key === invigilatorReportContentKeyInterface.national_id ||
      key === invigilatorReportContentKeyInterface.province_name
    ) {
      return false;
    }
    return true;
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: invigilatorReportContentInterface[] | null =
      this.state.reportContentExport;
    if (newArray) {
      for (const key in newArray) {
        if (this.isChangeAllowed(newArray[key].key)) {
          newArray[key].status = !currentState;
        }
      }

      this.setState({ reportContentExport: newArray });
      this.setSelectAll(!currentState);
      this.updateContentToPrint();
    }
  };

  setDownloading = (status: boolean) => {
    this.setState({ downloading: status });
  };

  FC_GeneratePDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      this.props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return false;
    }

    const table: HTMLElement | null = document.getElementById("report_content");
    let subject = "All Room";
    if (this.state.selectedCenter !== "all") {
      subject =
        "Room in " +
        this.state.selectedCenter.province_name +
        " province - " +
        this.state.selectedCenter.district_name +
        " district - " +
        this.state.selectedCenter.center_name +
        " district report";
    } else if (this.state.selectedDistrict !== "all") {
      subject =
        "Room in " +
        this.state.selectedDistrict.province_name +
        " province - " +
        this.state.selectedDistrict.district_name +
        " district report";
    } else if (this.state.selectedProvince !== "all") {
      subject =
        "Room in " +
        this.state.selectedProvince.province_name +
        " district report";
    }
    const reportSettings: reportSettingsInterface = {
      doneBy: this.props.auth.user.fname + " " + this.props.auth.user.lname,
      title: "Room Report",
      subjectTitle: "Room with invigilator",
      subject,
      period: "-",
      footer: "Online Recruitment",
      institute: "All institute",
    };
    table && generatePDF(reportSettings, table, this.setDownloading);
  };

  searchData = (
    objList: UserByTypeInterface[],
    text: string
  ): UserByTypeInterface[] => {
    if (undefined === text || text === "") return objList;
    return objList.filter((product) => {
      let flag;
      // let dataKeys = product;

      for (let prop in product) {
        flag = false;
        if (
          prop === "center_name" ||
          prop === "room_name" ||
          prop === "district_name" ||
          prop === "province_name" ||
          prop === "gender" ||
          prop === "first_name" ||
          prop === "last_name" ||
          prop === "national_id"
        ) {
          if (product[prop]) {
            flag =
              product[prop]!.toString()
                .toLowerCase()
                .indexOf(text.toLowerCase()) > -1;
          }
        }
        if (flag) break;
      }
      return flag;
    });
  };

  setChooseLocation = (status: "province" | "district" | "center" | null) => {
    this.setState({ chooseLocation: status });
  };

  setSelectedProvince = (data: LocationRoomTableInterface | "all") => {
    this.setState({
      selectedProvince: data,
      selectedDistrict: "all",
      selectedCenter: "all",
    });
  };

  setSelectedDistrict = (data: LocationRoomTableInterface | "all") => {
    this.setState({ selectedDistrict: data, selectedCenter: "all" });
  };

  setSelectedCenter = (data: LocationRoomTableInterface | "all") => {
    this.setState({ selectedCenter: data });
  };

  getNeededData = (
    location: LocationRoomTableInterface[],
    invigilators: UserByTypeInterfaceAPI[] | null
  ): UserByTypeInterface[] => {
    let result: UserByTypeInterface[] = [],
      room_ids: string[] = [];

    for (const keyForRoom in location) {
      if (Object.prototype.hasOwnProperty.call(location, keyForRoom)) {
        if (
          !isEmptyOrSpaces(location[keyForRoom].room_id) &&
          !room_ids.includes(location[keyForRoom].room_id)
        ) {
          room_ids.push(location[keyForRoom].room_id);
          const thisInvigilator = invigilators
            ? invigilators.find(
                (item) =>
                  item.roles_to_users.room_id === location[keyForRoom].room_id
              )
            : undefined;
          result.push({
            _id: location[keyForRoom].room_id, // ROOM ID
            role_id: thisInvigilator
              ? thisInvigilator.roles_to_users.room_id
              : null,
            role_name: thisInvigilator ? location[keyForRoom].room_name : null,
            role_key: UserByTypeKeys.INVIGILATOR,

            national_id: thisInvigilator ? thisInvigilator.n_id : null,
            phone_number: thisInvigilator ? thisInvigilator.phone_number : null,
            email: thisInvigilator ? thisInvigilator.email : null,
            first_name: thisInvigilator
              ? thisInvigilator.user_info.fname
              : null,
            last_name: thisInvigilator ? thisInvigilator.user_info.lname : null,
            gender: thisInvigilator ? thisInvigilator.user_info.sex : null,
            photo: thisInvigilator ? thisInvigilator.user_info.photo : null,
            province_id: location[keyForRoom].province_id,
            province_name: location[keyForRoom].province_name,
            district_id: location[keyForRoom].district_id,
            district_name: location[keyForRoom].district_name,
            center_id: location[keyForRoom].center_id,
            center_name: location[keyForRoom].center_name,
            room_id: location[keyForRoom].room_id,
            room_name: location[keyForRoom].room_name,
            school_id: "",
            school_name: "",
          });
        }
      }
    }

    return result;
  };

  FC_FilterReportData = (
    allData: UserByTypeInterface[],
    province: LocationRoomTableInterface | "all",
    district: LocationRoomTableInterface | "all",
    center: LocationRoomTableInterface | "all"
  ): UserByTypeInterface[] => {
    let result: UserByTypeInterface[] = allData;
    if (province !== "all") {
      result = result.filter(
        (item) => item.province_id === province.province_id
      );
    }
    if (district !== "all") {
      result = result.filter(
        (item) => item.district_id === district.district_id
      );
    }
    if (center !== "all") {
      result = result.filter((item) => item.center_id === center.center_id);
    }

    return result;
  };

  render() {
    return (
      <div>
        <SideNavigation>
          <div className="">
            <div className="text-gray-600 text-sm mb-2">Province:</div>
            {this.state.reportMainData && (
              <div
                onClick={() => this.setChooseLocation("province")}
                className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
              >
                {this.state.selectedProvince !== "all"
                  ? `${this.state.selectedProvince.province_name}`
                  : "All province"}
                <FiEdit className="text-lg text-blue-600" />
              </div>
            )}
          </div>
          {this.state.selectedProvince !== "all" && (
            <div className="">
              <div className="text-gray-600 text-sm mb-2">District:</div>
              {this.state.reportMainData && (
                <div
                  onClick={() => this.setChooseLocation("district")}
                  className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
                >
                  {this.state.selectedDistrict !== "all"
                    ? `${this.state.selectedDistrict.district_name}`
                    : "All district"}
                  <FiEdit className="text-lg text-blue-600" />
                </div>
              )}
            </div>
          )}
          {this.state.selectedDistrict !== "all" && (
            <div className="">
              <div className="text-gray-600 text-sm mb-2">Center:</div>
              {this.state.reportMainData && (
                <div
                  onClick={() => this.setChooseLocation("center")}
                  className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
                >
                  {this.state.selectedCenter !== "all"
                    ? `${this.state.selectedCenter.center_name}`
                    : "All center"}
                  <FiEdit className="text-lg text-blue-600" />
                </div>
              )}
            </div>
          )}
        </SideNavigation>

        {/* BODY CONTENT--------------------------------- */}
        <section>
          {/* REPORT TITLE ============================================ */}
          <div className="grid md:grid-cols-12">
            <div className="col-span-8">
              <div className="mt-4 mx-4 flex flex-col gap-1">
                <span className="text-3xl font-bold text-gray-700">
                  Room with invigilator report
                </span>
              </div>
            </div>
            <div className="col-span-4">
              <div className="float-right mt-2">
                {this.state.reportMainData &&
                  this.state.reportMainData.length > 0 && (
                    <div className="flex flex-row items-center justify-end font-bold mb-2">
                      <BiExport className="text-2xl text-gray-500" />
                      <span className="text-gray-500">Export as</span>
                      <button
                        className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                        disabled={this.state.reportMainData ? false : true}
                        onClick={() => {
                          this.updateContentToPrint();
                          this.setGenerateReportModal({
                            reportType: "EXCEL",
                            status: true,
                          });
                        }}
                      >
                        EXCEL
                      </button>
                      <button
                        className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 ${
                          this.state.downloading && "cursor-wait"
                        }`}
                        onClick={() => this.FC_GeneratePDF()}
                        disabled={
                          this.state.reportMainData && !this.state.downloading
                            ? false
                            : true
                        }
                      >
                        PDF
                      </button>
                    </div>
                  )}
              </div>
            </div>
          </div>
          {/* END REPORT TITLE ============================================ */}

          {/* ######################################################## */}
          <div className="bg-white my-4">
            <div className="w-full px-2 py-2">
              <input
                onChange={(e) => this.setState({ searchInput: e.target.value })}
                type="text"
                value={this.state.searchInput}
                placeholder="Search..."
                className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
                disabled={
                  this.state.reportMainData === null ||
                  this.FC_FilterReportData(
                    this.getNeededData(
                      LocationsInArray(this.props.locations),
                      this.state.reportMainData
                    ),
                    this.state.selectedProvince,
                    this.state.selectedDistrict,
                    this.state.selectedCenter
                  ).length <= 0
                }
              />
            </div>
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("province_name")}
                  >
                    <div className="flex items-center gap-1">
                      Province <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("district_name")}
                  >
                    <div className="flex items-center gap-1">
                      District <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("center_name")}
                  >
                    <div className="flex items-center gap-1">
                      Center <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("room_name")}
                  >
                    <div className="flex items-center gap-1">
                      Room number <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("fname")}
                  >
                    <div className="flex items-center gap-1">
                      Invigilator <BiSort />
                    </div>
                  </td>
                </tr>
              </thead>
              <tbody>
                {this.state.reportMainData === null ? (
                  <TableLoading cols={7} />
                ) : this.searchData(
                    this.FC_FilterReportData(
                      this.getNeededData(
                        LocationsInArray(this.props.locations),
                        this.state.reportMainData
                      ),
                      this.state.selectedProvince,
                      this.state.selectedDistrict,
                      this.state.selectedCenter
                    ),
                    this.state.searchInput
                  ).length === 0 ? (
                  <tr>
                    <td colSpan={7} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          No result found!{" "}
                          {this.state.searchInput.length > 0 &&
                            "Please try to search again"}
                        </span>
                        {(this.state.selectedProvince !== "all" ||
                          this.state.selectedDistrict !== "all" ||
                          this.state.selectedCenter !== "all") && (
                          <div className="w-full mt-2 flex flex-row items-center justify-center">
                            <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                              {this.state.selectedProvince !== "all" && (
                                <li
                                  onClick={() =>
                                    this.setChooseLocation("province")
                                  }
                                  className="underline text-gray-500 cursor-pointer"
                                >
                                  Please try another province!
                                </li>
                              )}
                              {this.state.selectedDistrict !== "all" && (
                                <li
                                  onClick={() =>
                                    this.setChooseLocation("district")
                                  }
                                  className="underline text-gray-500 cursor-pointer"
                                >
                                  Please try another district!
                                </li>
                              )}
                              {this.state.selectedCenter !== "all" && (
                                <li
                                  onClick={() =>
                                    this.setChooseLocation("center")
                                  }
                                  className="underline text-gray-500 cursor-pointer"
                                >
                                  Please try another center!
                                </li>
                              )}
                            </ul>
                          </div>
                        )}
                      </div>
                    </td>
                  </tr>
                ) : (
                  sortThisArray(
                    this.state.isSorted,
                    this.searchData(
                      this.FC_FilterReportData(
                        this.getNeededData(
                          LocationsInArray(this.props.locations),
                          this.state.reportMainData
                        ),
                        this.state.selectedProvince,
                        this.state.selectedDistrict,
                        this.state.selectedCenter
                      ),
                      this.state.searchInput
                    ),
                    this.state.sortProperty,
                    this.state.sortOrder
                  ).map((item, i) => (
                    <tr
                      key={i + 1}
                      className={`hover:bg-blue-100 hover:text-blue-700 border-b hover:border-blue-500 text-sm ${
                        !item.n_id
                          ? "border-yellow-500 text-gray-700 bg-gray-50"
                          : "text-gray-600 border-gray-300 bg-white"
                      }`}
                    >
                      <td className="px-2 py-2">{i + 1}</td>
                      <td className="px-2 py-2">{item.province_name}</td>
                      <td className="px-2 py-2">{item.district_name}</td>
                      <td className="px-2 py-2">{item.center_name}</td>
                      <td className="px-2 py-2">{item.room_name}</td>
                      <td className="px-2 py-2">
                        {item.fname ? item.fname + " " + item.lname : "-"}
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
          {/* ######################################################## */}
        </section>
        {/* END BODY CONTENT ============================================ */}

        {this.state.chooseLocation && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                <MdLocationCity className="text-5xl text-blue-600" />
                <span className="flex flex-col">
                  Filter by {capitalizeFirstLetter(this.state.chooseLocation)}
                </span>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => this.setChooseLocation(null)}
            backDropClose={true}
            widthSizeClass={ModalSize.small}
            displayClose={true}
            padding={{ title: true }}
          >
            <ChooseLocation
              setChooseLocation={this.setChooseLocation}
              locationList={LocationsInArray(this.props.locations)}
              setSelectedLocation={
                this.state.chooseLocation === "province"
                  ? this.setSelectedProvince
                  : this.state.chooseLocation === "district"
                  ? this.setSelectedDistrict
                  : this.setSelectedCenter
              }
              loadindRequest={this.state.loading}
              selectedLocation={
                this.state.chooseLocation === "province"
                  ? this.state.selectedProvince
                  : this.state.chooseLocation === "district"
                  ? this.state.selectedDistrict
                  : this.state.selectedCenter
              }
              locationType={this.state.chooseLocation}
              isModal={true}
            />
          </Modal>
        )}

        {this.state.generateReportModal !== null &&
          this.state.reportContentExport &&
          this.state.generateReportModal.status === true && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Generating Report contents
                    </span>
                    <span className="text-sm text-blue-600 font-normal">
                      Generating {this.state.generateReportModal.reportType}{" "}
                      Report
                    </span>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setGenerateReportModal(null)}
              backDropClose={false}
              widthSizeClass={ModalSize.medium}
              displayClose={true}
              padding={{ title: true }}
            >
              <div className="px-6">
                <div className="my-2 text-base text-gray-700 sm:text-lg">
                  <div className="flex flex-col">
                    <label>Title</label>
                    <input
                      type="text"
                      className={`text-left input-white w-20 inline-block ${
                        this.state.formError &&
                        this.state.formError.element === "report_title"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder="Sub Category Title"
                      value={this.state.reportTitle}
                      onChange={(e) => {
                        this.setFormError(null);
                        this.setReportTitle(e.target.value);
                      }}
                      disabled={this.state.reportMainData ? false : true}
                    />
                    {this.state.formError &&
                      this.state.formError.element === "report_title" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                  <div className="flex flex-col mt-2">
                    <label>Report content</label>
                    <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                      {this.state.reportContentExport.map((item, index) => (
                        <div
                          key={index}
                          className={`flex items-center gap-1 ${
                            this.isChangeAllowed(item.key)
                              ? "cursor-pointer"
                              : "cursor-not-allowed"
                          }`}
                          onClick={() =>
                            this.isChangeAllowed(item.key) &&
                            this.updateReportContent(item.key)
                          }
                          title={
                            !this.isChangeAllowed(item.key)
                              ? "Required for every report!"
                              : ""
                          }
                        >
                          {item.status ? (
                            <MdCheckBox className="w-6 h-6 text-blue-700" />
                          ) : (
                            <MdCheckBoxOutlineBlank className="w-6 h-6" />
                          )}
                          <span>{item.title}</span>
                        </div>
                      ))}
                    </div>
                    {this.state.formError &&
                      this.state.formError.element === "report_content" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                </div>
                <div className="flex justify-between mb-2 mt-4">
                  <div
                    className={`flex items-center gap-1 cursor-pointer`}
                    onClick={() =>
                      this.resetReportContent(this.state.selectAll)
                    }
                  >
                    {this.state.selectAll ? (
                      <MdCheckBox className="w-6 h-6 text-blue-700" />
                    ) : (
                      <MdCheckBoxOutlineBlank className="w-6 h-6" />
                    )}
                    <span>Select all</span>
                  </div>

                  {this.state.centerToPrint && this.state.reportMainData && (
                    <ExportToExcel
                      fileData={this.state.centerToPrint}
                      fileName={this.state.reportTitle}
                      btnName="Generate"
                    />
                  )}
                </div>
              </div>
            </Modal>
          )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  locations,
}: StoreState): {
  auth: Auth;
  locations: RoomLocationInterface[];
} => {
  return {
    auth: auth,
    locations: locations.roomLocation,
  };
};

export const Invigilator = connect(mapStateToProps, {
  getAllRoom,
  PushPopupAlert,
})(index);
