import React from "react";
// import { MdSearch } from "react-icons/md";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import { LocationRoomTableInterface } from "../../../shared/interfaces";

type RoleListProps = {
  setChooseLocation: (value: "province" | "district" | "center" | null) => void;
  locationList: LocationRoomTableInterface[];
  setSelectedLocation: (data: LocationRoomTableInterface | "all") => void;
  selectedLocation: LocationRoomTableInterface | "all";
  loadindRequest: boolean;
  bgWhite?: boolean;
  isModal?: boolean;
  locationType: "province" | "district" | "center";
};

const ChooseLocation: React.FC<RoleListProps> = (props) => {
  const distinctData = (
    allData: LocationRoomTableInterface[]
  ): LocationRoomTableInterface[] => {
    let result: LocationRoomTableInterface[] = [],
      ids: string[] = [];

    for (const key in allData) {
      if (Object.prototype.hasOwnProperty.call(allData, key)) {
        if (
          props.locationType === "province" &&
          !ids.includes(allData[key].province_id)
        ) {
          ids.push(allData[key].province_id);
          result.push(allData[key]);
        } else if (
          props.locationType === "district" &&
          !ids.includes(allData[key].district_id)
        ) {
          ids.push(allData[key].district_id);
          result.push(allData[key]);
        } else if (
          props.locationType === "center" &&
          !ids.includes(allData[key].center_id)
        ) {
          ids.push(allData[key].center_id);
          result.push(allData[key]);
        }
      }
    }
    ids = [];
    return result;
  };
  return (
    <section
      className={`${
        props.isModal ? "" : "border-2 rounded border-gray-400 shadow-md"
      }`}
    >
      <div
        className={`w-full h-52 mt-2 ${
          props.isModal ? "px-2" : ""
        } overflow-y-auto divide-y divide-gray-300 ${
          props.bgWhite && "bg-white"
        }`}
      >
        <section>
          <div
            className={`group flex items-center justify-between my-1 ${
              props.selectedLocation === "all"
                ? "cursor-default bg-blue-50 hover:bg-blue-100"
                : "hover:bg-blue-100"
            }`}
          >
            <div
              className={`p-1 w-full mr-2 ${
                props.selectedLocation === "all"
                  ? "cursor-default"
                  : "cursor-pointer"
              }`}
              onClick={() => {
                props.setChooseLocation(null);
                !props.loadindRequest &&
                  props.selectedLocation !== "all" &&
                  props.setSelectedLocation("all");
              }}
            >
              <p className="text-sm font-medium text-black">
                All {props.locationType}
              </p>
            </div>
            <button
              type="button"
              className="bg-blue-200 group-hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 p-1 mr-1 rounded text-sm"
            >
              Choose
            </button>
          </div>
        </section>
        {distinctData(props.locationList).length > 0 ? (
          distinctData(props.locationList).map((element, index) => (
            <section key={index + element.center_id}>
              <div className="group flex items-center justify-between my-1 hover:bg-gray-100">
                <div
                  className="p-1 w-full mr-2 cursor-pointer"
                  onClick={() => {
                    props.setChooseLocation(null);
                    !props.loadindRequest && props.setSelectedLocation(element);
                  }}
                >
                  <p className="text-sm font-medium text-black">
                    {capitalizeFirstLetter(
                      props.locationType === "province"
                        ? element.province_name
                        : props.locationType === "district"
                        ? element.district_name
                        : element.center_name
                    )}
                  </p>
                </div>
                <button
                  type="button"
                  className="bg-blue-200 group-hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 p-1 mr-1 rounded text-sm"
                >
                  Choose
                </button>
              </div>
            </section>
          ))
        ) : (
          <div className="text-center p-3">
            <p className="text-base font-medium text-black">
              No district available
            </p>
          </div>
        )}
      </div>
    </section>
  );
};

export default ChooseLocation;
